import { useEffect, useState } from "react";
let _distanceWithTop = 0
let _heightSection = 0
export const Strategy = () => {
  const [isShowText, showText] = useState(false)
  useEffect(() => {
    const _strategy = document.getElementById('strategy')
    _heightSection = _strategy.offsetHeight
    _distanceWithTop = _strategy.offsetTop
  },[])
  useEffect(() => {
    function handleScroll() {
      const _position = window.scrollY - _distanceWithTop
      if (_position > -_heightSection && _position < 700) {
        showText(true)
      } else {
        showText(false)
      }
    }

    window.addEventListener('scroll', handleScroll)
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  });
  return (
    <div className="section strategy" id="strategy">
    <div className="section__divider"></div>
    <div className="container">
      <div className="row">
        <div className="col-lg-8 col-12">
          <div className="section__image">
            <img src="assets/images/strategy-image.png" alt="strategy"/>
            <div className="section__image-particle">
              <img src="assets/images/particle.png" alt="particle"/>
            </div>
          </div>
        </div>
        <div className="col-lg-4 col-12 mx-auto">
          <div className={`section__content ${isShowText ? 'animate' : ''}`}>
            <div className="section__content-title">Best Strategy Idle-RPG game</div>
            <div className="section__content-text">Contrary to popular belief, Lorem Ipsum is not simply random text. It has roots in a piece of classical Latin literature from 45 BC, making it over 2000 years old. Richard McClintock, a Latin professor at Hampden-Sydney College in Virginia, looked up one of the more obscure Latin words, consectetur, from a Lorem Ipsum passage, and going through the cites of the word in classical literature, discovered the undoubtable source. </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  )
}