import { useEffect, useCallback, useState } from 'react';
import * as PIXI from 'pixi.js';
import { Stage } from '@inlet/react-pixi';
import Spine from '../Spine';
const LIST_SOURCE = {
  1: {
    name: 'HolyKnight',
    path: '/assets/elements/HolyKnight/HolyKnight.json'
  },
  2: {
    name: 'Osse',
    path: '/assets/elements/Osse/Osse.json'
  },
  3: {
    name: 'cernunos',
    path: '/assets/elements/cernunos/cernunos.json'
  },
  4: {
    name: 'Eitri',
    path: '/assets/elements/Eitri/Eitri.json'
  },
  5: {
    name: 'Diadora',
    path: '/assets/elements/Diadora/Diadora.json'
  },
  6: {
    name: 'Zygor',
    path: '/assets/elements/Zygor/Zygor.json'
  }
};
const mixes = [];
// [
//   {
//     from: 'idle',
//     to: 'dead',
//     duration: 0.2,
//   },
//   {
//     from: 'dead',
//     to: 'idle',
//     duration: 0.4,
//   },
// ];
export const Element = props => {
  const { id } = props;
  const [spineData, setSpineData] = useState();
  const [width, setWidth] = useState(900)
  const [height, setHeight] = useState(800)
  const [scale, setScale] = useState(0.8)
  useEffect(() => {
    const _windowWidth = window.innerWidth
    if (_windowWidth < 900 && _windowWidth > 400) {
      setWidth(_windowWidth)
      setHeight(600)
      setScale(0.5)
    }
    if (_windowWidth < 400) {
      setWidth(_windowWidth - 50)
      setHeight(500)
      setScale(0.4)
    }
  }, [])
  useEffect(() => {
    const initSpine = async () => {
      await import('pixi-spine');
      const loader = new PIXI.Loader();
      const _element = LIST_SOURCE[id] || LIST_SOURCE[1]
      loader
        .add(_element.name, _element.path)
        .load((loader, res) => {
          if (res[_element.name].spineData) {
            const _spineData = res[_element.name].spineData
            // const animation = new PIXI.spine.Spine(_spineData);

            // const skin = new PIXI.spine.core.Skin("custom");
            // skin.addSkin(_spineData.findSkin('default'));
            // skin.addSkin(_spineData.findSkin('skin_1'));
            // animation.skeleton.setSkin(skin);

            setSpineData(_spineData);
          }
        });
    };
    setSpineData(null);
    initSpine();
  }, [id]);

  const stateRef = useCallback((state) => {
    if (state) {
      // state.setAnimation(0, 'idle', false);
      state.addAnimation(0, 'idle', true, 0);
    }
    // state.addAnimation(0, 'idle', true, 0);
  }, []);

  return (
    <div className="element_canvas">
      <Stage width={width} height={height} options={{ transparent: true }}>
        {spineData && (
          <Spine
            x={width/2}
            y={height - 100}
            spineData={spineData}
            scale={scale}
            mixes={mixes}
            animationStateCallback={stateRef}
          />
        )}
      </Stage>
    </div>
  );
};