import {Element} from "../Element"
import Swiper from 'swiper';
// import Swiper styles
// import 'swiper/swiper-bundle.min.css'
import 'swiper/swiper.min.css'
import { useEffect, useState } from "react";
const MAX_ELEMENT = 6
export const ListElement = () => {
  const [currentElement, setCurrentElement] = useState(1)
  useEffect(() => {
    new Swiper('.elementSwiper', {
      slidesPerView: 1,
      spaceBetween: 30,
      loop: true,
      autoplay: {
        delay: 5000,
        disableOnInteraction: false
      },
      breakpoints: {
        576: {
          slidesPerView: 3
        },
        1200: {
          slidesPerView: 5
        }
      },
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
      }
    })
  }, [])
  const next = () => {
    const _nextCurrentElement = currentElement + 1
    setCurrentElement(_nextCurrentElement > MAX_ELEMENT ? 1 : _nextCurrentElement)
  }
  const prev = () => {
    const _prewCurrentElement = currentElement - 1
    setCurrentElement(_prewCurrentElement < 1 ? 1 : _prewCurrentElement)
  }
  return (
    <div className="section element">
    <div className="section__background"></div>
    <div className="containner">
      <div className="row">
        <div className="col-lg-10 col-12 mx-auto">
          <div className="section__content text-center">
            <div className="section__content-title">The Epic Elements</div>
          </div>
        </div>
        <div className="col-12">
						<div className="swiper element-tab elementSwiper">
							<div className="swiper-wrapper">
								<div className="swiper-slide">
									<div className="element__item">
										<div className="element__item-image">
											<img src="assets/images/element-item-01.png" alt="element-item" className="mx-auto"/>
										</div>
										<div className="element__item-content">Land of Abyss</div>
									</div>
								</div>
								<div className="swiper-slide">
									<div className="element__item">
										<div className="element__item-image">
											<img src="assets/images/element-item-02.png" alt="element-item" className="mx-auto"/>
										</div>
										<div className="element__item-content">Heroes of Light</div>
									</div>
								</div>
								<div className="swiper-slide">
									<div className="element__item">
										<div className="element__item-image">
											<img src="assets/images/element-item-03.png" alt="element-item" className="mx-auto"/>
										</div>
										<div className="element__item-content">Kingdom of Darkness</div>
									</div>
								</div>
								<div className="swiper-slide">
									<div className="element__item">
										<div className="element__item-image">
											<img src="assets/images/element-item-04.png" alt="element-item" className="mx-auto"/>
										</div>
										<div className="element__item-content">Land of Nature</div>
									</div>
								</div>
								<div className="swiper-slide">
									<div className="element__item">
										<div className="element__item-image">
											<img src="assets/images/element-item-05.png" alt="element-item" className="mx-auto"/>
										</div>
										<div className="element__item-content">Marine Union</div>
									</div>
								</div>
							</div>
						</div>
            <div onClick={next} className="swiper-button-next"></div>
              <div><Element id={currentElement}/></div>
							<div onClick={prev} className="swiper-button-prev"></div>
					</div>
      </div>
    </div>
  </div>
  )
}